import { h, Component } from 'preact';
import { Router } from 'preact-router';
import { createHashHistory } from 'history';

import util from '../lib/util';
import PubSub, { topics } from '../lib/pubsub';

import appState from '../stores/appstate';
import waypointStore from '../stores/waypoint';

import Header from './header';
import Login from './login';

// Code-splitting is automated for routes
import Home from '../routes/home';
import Profile from '../routes/profile';

const API_SERVER = 'http://localhost:60001';

export default class App extends Component {
	constructor(props) {
        super(props);
		this.stores = {
            appState,
            waypointStore,
        };
        const apiServer = API_SERVER || props.apiServer || `${window.location.protocol}//${window.location.host}`;
        util.setApiServer(apiServer);

        this.state = {
            jwtToken: util.getJwtToken(),
        };

        this.subscriptions();
    }

    subscriptions() {
        PubSub.subscribe(topics.LOG_OUT, () => {
            this.setState({
                jwtToken: undefined,
            });
            util.removeUserEmail();
            util.removeJwtToken();
        });

        PubSub.subscribe(topics.JWT_TOKEN_CHANGED, async (token) => {
            util.setJwtToken(token);
            this.setState({
                jwtToken: token,
            });
        });
    }

	/** Gets fired when the route changes.
	 *	@param {Object} event		"change" event from [preact-router](http://git.io/preact-router)
	 *	@param {string} event.url	The newly routed URL
	 */
	handleRoute = e => {
		this.currentUrl = e.url;
	};

	render() {
		if (!this.state.jwtToken) {
            return (
				<div id='app'>
					<Login stores={this.stores} {...this.props} />
				</div>
			);		
        }
		return (
			<div id='app'>
				<Header />
				<Router onChange={this.handleRoute} history={createHashHistory()}>
					<Home stores={this.stores} path="/" />
					<Profile stores={this.stores} path="/profile/" user="me" />
					<Profile stores={this.stores} path="/profile/:user" />
				</Router>
			</div>
		);
	}
}
